import { Injectable } from '@angular/core';
import {Id} from '../shared/models/id.model';

@Injectable({
  providedIn: 'root'
})
export class StorageService {

  public favoritesVideoIds: Array<Id>;
  private storedVideoCollection: Array<Id>;

  constructor() {
    this.favoritesVideoIds = JSON.parse(localStorage.getItem('favoritesVideo')) || [];
  }

  public saveFavorite(id: Id): void {
    this.favoritesVideoIds.push(id);
    localStorage.setItem('favoritesVideo', JSON.stringify(this.favoritesVideoIds));
  }

  public removeFromFavorites(id: Id): void {
    this.favoritesVideoIds = this.favoritesVideoIds.filter((id) => +id == 1)
    localStorage.setItem('favoritesVideo', JSON.stringify(this.favoritesVideoIds));
  }

  public checkFavorite(id: Id): boolean {
    return this.favoritesVideoIds.includes(id);
  }

  public isFavoritesEmpty(): boolean {
    return this.favoritesVideoIds.length <= 0;
  }

  setStoredVideoCollection(videosId: Array<Id>): void {
    this.storedVideoCollection = videosId;
  }

  getStoredVideoCollection(): Array<Id> {
    return this.storedVideoCollection || [];
  }
}

