import {Component, OnInit} from '@angular/core';
import {Video} from '../../shared/models/video.model';
import {StorageService} from '../../services/storage.service';
import {VideoService} from '../../services/video.service';
import {YoutubeGatewayService} from '../../services/youtube-gateway.service';
import {ActivatedRoute, Params} from '@angular/router';
import {Url} from '../../shared/models/id.model';

@Component({
  selector: 'favorites',
  templateUrl: './favorites.component.html',
  styleUrls: ['./favorites.component.scss']
})
export class FavoritesComponent implements OnInit {

  public activeVideo: Video;
  public isFavoritesEmpty: boolean = this.storage.isFavoritesEmpty();
  private readonly currentComponentName: string = 'Favorites';

  constructor(
    private route: ActivatedRoute,
    private storage: StorageService,
    private videoService: VideoService,
    private youtubeGatewayService: YoutubeGatewayService,
    ) { }

  ngOnInit() {
    this.route.params.subscribe((params: Params) => {
      this.activeVideo = this.youtubeGatewayService.getVideoById(params.id);
    })
  }

  public getBackgroundPoster(video: Video, componentName = this.currentComponentName): Url {
    return this.videoService.getVideoPoster(video, componentName);
  }
}
