import { Injectable } from '@angular/core';
import {BehaviorSubject, Observable} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AppService {

  private isPreloader: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
  private preloaderCounter: number = 0;

  constructor() { }

  public get isPreloader$(): Observable<boolean> {
    return this.isPreloader.asObservable();
  }

  public switchPreloader(isOn = true) {
    const execute = this.multiSwitcherCheck(isOn);
    if (execute) {
      this.isPreloader.next(isOn);
    }
  }

  private multiSwitcherCheck(value: boolean): boolean {
    let execute = false;
    let counter = this.preloaderCounter;
    if (value) {
      counter++;
      if (counter === 1) {
        execute = true;
      }
    } else {
      counter--;
      if (counter <= 0) {
        counter = 0;
        execute = true;
      }
    }
    this.preloaderCounter = counter;
    return execute;
  }
}
