import {ActivatedRouteSnapshot, Resolve, RouterStateSnapshot} from '@angular/router';
import {Observable} from 'rxjs';
import {YoutubeGatewayService} from '../services/youtube-gateway.service';
import {Injectable} from '@angular/core';
import {Video} from '../shared/models/video.model';

@Injectable({ providedIn: 'root' })
export class ActiveVideoResolver implements Resolve<Video>{

  constructor(private youtubeGatewayService: YoutubeGatewayService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<Video> | Promise<Video> | Video {
    return this.youtubeGatewayService.getVideoById(route.queryParams.id);
  }
}
