import {Component, ElementRef, OnInit, Optional, SkipSelf, ViewChild} from '@angular/core';
import {Tag, TagsColors} from '../shared/models/tag.model';
import {AppPaths} from '../app-paths';
import {Route} from '../shared/models/route.model';
import {KEYS, KeysService, NavBase, NavigatableComponent, provideNavigatable} from '@alignedcode/tvbase';

@Component({
  selector: 'category-carousel',
  templateUrl: './category-carousel.component.html',
  styleUrls: ['./category-carousel.component.scss'],
})
export class CategoryCarouselComponent extends NavigatableComponent implements OnInit {

  @ViewChild('categoryCarouselItem',{read: ElementRef, static: false}) categoryCarouselItem: ElementRef;
  private TAGS_PER_PAGE: number = 3;
  private activeOffset: number = 0;
  public tags: Array<Tag> = [];
  public CATEGORIES_ROUTE: Route = AppPaths.CATEGORIES;
  public activeTagIndex: number = 0;
  public tagContainerLeftPos: number = 0;

  constructor(
    @SkipSelf()
    @Optional()
    parent: NavBase,
    keysService: KeysService,
    elementRef: ElementRef,
  ) {
    super(elementRef, parent, keysService,new Map([
      [KEYS.LEFT, () => {if(this.isActive())this.prevTag()}],
      [KEYS.RIGHT, () => {if(this.isActive())this.nextTag()}]
    ]))
  }

  ngOnInit() {
    let index: number = 0;
    for (let tag in TagsColors){
      this.tags[index] = tag;
      index++;
    }

  }

  calcOffset() {
    this.setupActiveOffset();
  }

  setupActiveOffset(){
    const tagCollection = this.categoryCarouselItem.nativeElement as HTMLElement;
    const tagWidth = tagCollection.getBoundingClientRect().width;
    this.activeOffset = tagWidth;
  }

  private shiftTags(offset): void {
    this.tagContainerLeftPos = this.tagContainerLeftPos + offset;
  }

  public nextTag(): void {
    this.calcOffset();
    if (this.activeTagIndex >= 0) {
      this.shiftTags(-this.activeOffset);
    }
    if (this.activeTagIndex === this.tags.length - 1) {
      this.activeTagIndex = 0;
      this.tagContainerLeftPos = 0;
    } else {
      this.activeTagIndex++;
    }
  }

  public prevTag(): void {
    this.calcOffset();
    if (this.activeTagIndex !== 0) {
      this.activeTagIndex--;
      this.shiftTags(this.activeOffset);
    }
  }

  isActive(){
    const dom: HTMLElement = this.elementRef.nativeElement as HTMLElement;
    return  !!dom.querySelectorAll('.category-carousel-item.hover').length;
  }

  trackByFn(index, item) {
    return item.id;
  }
}
