import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { HttpGateway } from './services/http-gateway.service';
import { EnvironmentService } from './services/environment.service';
import {
  TVBaseModule,
  PlatformService,
  WebOsPlatform,
  TizenPlatform,
  DesktopPlatform,
  NetcastPlatform,
  OrsayPlatform,
  AmazonFireTvPlatform
} from '@alignedcode/tvbase';
import { HttpClientModule } from '@angular/common/http';
import { routing } from './app-routing';
import { HomeComponent } from './pages/home/home.component';
import { HeaderNavComponent } from './header-nav/header-nav.component';
import { VideoCarouselComponent } from './video-carousel/video-carousel.component';
import { VideoCarouselItemComponent } from './video-carousel/video-carousel-item/video-carousel-item.component';
import { VideoDetailsComponent } from './video-details/video-details.component';
import { ErrorComponent } from './pages/error/error.component';
import { CategoriesComponent } from './pages/categories/categories.component';
import { CategoryDetailsComponent } from './pages/categories/category-details/category-details.component';
import { CategoryCarouselComponent } from './category-carousel/category-carousel.component';
import { CategoryCarouselItemComponent } from './category-carousel/category-carousel-item/category-carousel-item.component';
import { FavoritesComponent } from './pages/favorites/favorites.component';
import { FavoritesDescriptionComponent } from './pages/favorites/favorites-description/favorites-description.component';
import { FavoritesCarouselComponent } from './pages/favorites/favorites-carousel/favorites-carousel.component';
import { PreloaderComponent } from './preloader/preloader.component';
import {VideoPlayerModule} from './video-player/video-player.module';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    HeaderNavComponent,
    VideoCarouselComponent,
    VideoCarouselItemComponent,
    VideoDetailsComponent,
    ErrorComponent,
    CategoriesComponent,
    CategoryDetailsComponent,
    CategoryCarouselComponent,
    CategoryCarouselItemComponent,
    FavoritesComponent,
    FavoritesDescriptionComponent,
    FavoritesCarouselComponent,
    PreloaderComponent,
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    TVBaseModule,
    routing,
    VideoPlayerModule
  ],
  providers: [
    HttpGateway,
    EnvironmentService,
    PlatformService,
    DesktopPlatform,
    WebOsPlatform,
    TizenPlatform
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
  constructor(platformService: PlatformService) {
    platformService.add(WebOsPlatform);
    platformService.add(TizenPlatform);
    platformService.add(NetcastPlatform);
    platformService.add(OrsayPlatform);
    platformService.add(AmazonFireTvPlatform);
    platformService.add(DesktopPlatform);
  }
}
