import {Injectable} from '@angular/core';
import {Video} from '../shared/models/video.model';
import axios from 'axios';
import {VideoService} from './video.service';
import {Id} from '../shared/models/id.model';
import {VideoAdapterService} from './video-adapter.service';
import {AppService} from '../app.service';


@Injectable({
  providedIn: 'root'
})
export class YoutubeGatewayService {

  private youtubeApiUrl: string = 'https://www.googleapis.com/youtube/v3';
  private youtubePlaylistId: string = 'PLco8R-Zc6LC45ZinDPA3YEss99K-vN2LZ';
  private youtubeApiKey: string = 'AIzaSyD3uky03hyKTqoGp1vtupzgG2Vbsa9URAI';
  private videoList: Array<Video> = [];
  private cache: object = {};
  public getPlaylistVideosWithCache = this.memoization(this.getPlaylistVideos.bind(this)).bind(this);
  public getVideoDurationWithTagsWithCache = this.memoization(this.getVideoDurationWithTags.bind(this)).bind(this);

  constructor(
    private videoService: VideoService,
    private videoAdapterService: VideoAdapterService,
    private appService: AppService,
    ) {
  }
//TODO: rebuild to optimize
  public async getPlaylistVideos() {
    this.appService.switchPreloader(true);
    const response = await axios.get(`${this.youtubeApiUrl}/playlistItems?part=snippet&playlistId=${this.youtubePlaylistId}&maxResults=50&key=${this.youtubeApiKey}`);
    this.appService.switchPreloader(false);
    const {items = []} = await response.data;
    this.videoList = await this.videoAdapterService.videoConversion(items);
    for (let i = 0; i < this.videoList.length; i++) {
      this.videoList[i].duration = (await this.getVideoDurationWithTagsWithCache(this.videoList[i].id)).duration;
      this.videoList[i].tags = (await this.getVideoDurationWithTagsWithCache(this.videoList[i].id)).tags;
    }
    return this.videoList;
  }

  public memoization(func):any {
    return (...args) => {
      let n = args[0];
      if (n in this.cache) {
        return this.cache[n];
      }
      else {
        let result = func(n);
        this.cache[n] = result;
        return result;
      }
    }
  }

  public getVideoById(videoId: Id): Video{
    return this.videoList.find(({id}) => id == videoId);
  }

  public getNextVideoById(videoId: Id): Video{
    const nextVideoIndex: number = this.videoList.indexOf(this.videoList.find(({id}) => id == videoId)) + 1;
    return this.videoList[nextVideoIndex];
  }

  public async getVideoDurationWithTags(videoId: Id){
    const response = await axios.get(`${this.youtubeApiUrl}/videos?part=snippet%2CcontentDetails&id=${videoId}&key=${this.youtubeApiKey}`);
    const json = await response.data;
    return this.videoAdapterService.durationAndTagConversation(json);
  }
}
