import {Injectable} from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class VideoAdapterService {

  constructor() { }

  public async videoConversion(items: Array<any>) {
    const videoList = await items.map(video => ({
      id: video.snippet.resourceId.videoId,
      title: video.snippet.title,
      description: video.snippet.description,
      duration: '',
      tags: [],
      picture: {
        medium: {...video.snippet.thumbnails.medium}.url,
        high: {...video.snippet.thumbnails.high}.url,
        standard: {...video.snippet.thumbnails.standard}.url,
        maxres: {...video.snippet.thumbnails.maxres}.url,
      },
    }))
    return videoList;
  }
  public durationAndTagConversation(json: any) {
    return {
      tags: json.items[0].snippet.tags,
      duration: this.formatDuration(json.items[0].contentDetails.duration),
    }
  }

  private formatDuration(duration: string): string{
    return duration
      .replace('PT', '')
      .replace('M', ':')
      .replace('S', ' minutes');
  }
}
