import {Component, ElementRef, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Video} from '../../shared/models/video.model';
import {VideoService} from '../../services/video.service';
import {Tag, TagsColors} from '../../shared/models/tag.model';
import {Url} from '../../shared/models/id.model';
import {Router} from '@angular/router';
import {YoutubeGatewayService} from '../../services/youtube-gateway.service';

@Component({
  selector: 'video-carousel-item',
  templateUrl: './video-carousel-item.component.html',
  styleUrls: ['./video-carousel-item.component.scss']
})
export class VideoCarouselItemComponent implements OnInit {

  @Input() video: Video;
  @Output() changeActiveVideo = new EventEmitter<Video>();
  private readonly FIRST_PARAMETER_ROUTE_POS: number = 2;
  private readonly SECOND_PARAMETER_ROUTE_POS: number = 3;

  public readonly MAIN_TAG_POS: number = 0;

  constructor(
    private videoService: VideoService,
    private elementRef: ElementRef,
    private router: Router,
    private youtubeGatewayService: YoutubeGatewayService,
    ) { }

  ngOnInit() {
  }

  isActive(){
    const dom: HTMLElement = this.elementRef.nativeElement as HTMLElement;
    return !!dom.querySelectorAll('.video-carousel-item.hover').length;
  }

  public getVideoPoster(video: Video): Url {
      return this.videoService.getVideoTileImage(video);
  }

  public getTagColor(tag: Tag): string {
    return TagsColors[tag];
  }

  public isDefault():boolean {
    let activeVideoId;
    if (this.router.url.length == 17) {
      activeVideoId = this.router.url.split('/')[this.FIRST_PARAMETER_ROUTE_POS];
    }else { activeVideoId = this.router.url.split('/')[this.SECOND_PARAMETER_ROUTE_POS]}
    const activeVideo = this.youtubeGatewayService.getVideoById(activeVideoId);
    if (this.video == activeVideo) {
      return true;
    }
  }
}
