import {AfterViewChecked, ChangeDetectorRef, Component, ElementRef, Injectable, OnInit} from '@angular/core';
import {
  provideNavigatable,
  KeysService,
  NavigationService,
  NavigatableComponent,
  PlatformService
} from '@alignedcode/tvbase';
import {RouteEventsService} from './services/route-events.service';
import {interval, Subscription} from 'rxjs';
import {AppService} from './app.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  providers: [provideNavigatable(AppComponent)]
})
export class AppComponent extends NavigatableComponent implements OnInit{

  private subscription$: Subscription;
  public title: string = 'Fitness';
  public hasConnection: boolean = true;

  constructor(
    elementRef: ElementRef,
    keysService: KeysService,
    private navigationService: NavigationService,
    private routeEventsService: RouteEventsService,
    private platformService: PlatformService,
    private appService: AppService,
    private cdRef: ChangeDetectorRef,
  ) {
    super(elementRef, null, keysService);
    this.navigationService.setRoot(this);
    routeEventsService.loadRouting();
  }

  ngOnInit() {
    this.subscription$ = interval(5000).subscribe(_ => this.isNetworkConnected());
  }

  public isNetworkConnected() {
    this.hasConnection = this.platformService.hasNetwork();
  }

  public get isPreloader() {
    return this.appService.isPreloader$;
  }
}
