import {Component,OnInit} from '@angular/core';
import {Video} from '../../shared/models/video.model';
import {VideoService} from '../../services/video.service';
import {Tag, TagsColors} from '../../shared/models/tag.model';
import {ActivatedRoute, Params, Router} from '@angular/router';
import {YoutubeGatewayService} from '../../services/youtube-gateway.service';
import {Url} from '../../shared/models/id.model';
import {AppPaths} from '../../app-paths';

@Component({
  selector: 'categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.scss']
})
export class CategoriesComponent implements OnInit {

  public activeTag: Tag;
  public activeVideo: Video;
  public isFocusBtnBack: boolean = false;
  private currentComponentName: string = 'Categories';

  constructor(
    private videoService: VideoService,
    private route: ActivatedRoute,
    private router: Router,
    private youtubeGatewayService: YoutubeGatewayService,
    ) { }

  ngOnInit() {
    this.route.params.subscribe(async (params: Params) => {
      this.activeTag = params.tag;
      this.activeVideo = this.youtubeGatewayService.getVideoById(params.id);
    })
  }

  public getBackgroundPoster(video: Video, componentName = this.currentComponentName): Url {
    return this.videoService.getVideoPoster(video, componentName);
  }

  public getTagColor(tag: Tag): string {
    if(!!tag){
      return TagsColors[tag];
    }
  }

  public setFocusBtnBack(): void {
    this.isFocusBtnBack = true;
  }

  public setUnfocusBtnBack(): void {
    this.isFocusBtnBack = false;
  }

  public back(): void {
    this.activeVideo = undefined;
    this.router.navigate([`/${AppPaths.CATEGORIES}`])
  }
}
