import { Component, OnInit } from '@angular/core';
import {AppPaths} from '../app-paths';
import {Route} from '../shared/models/route.model';

@Component({
  selector: 'header-nav',
  templateUrl: './header-nav.component.html',
  styleUrls: ['./header-nav.component.scss']
})
export class HeaderNavComponent implements OnInit {


  public readonly HOME_ROUTE: Route = AppPaths.HOME;
  public readonly CATEGORIES_ROUTE: Route = AppPaths.CATEGORIES;
  public readonly FAVOURITES_ROUTE: Route = AppPaths.FAVOURITES;
  public isFocused: boolean = false;

  constructor() { }

  ngOnInit() {
  }

  public activateNav(isFocused: boolean = true): void {
    setTimeout( () => this.isFocused = isFocused,0)
  }
}
