import {Component,Input,OnInit} from '@angular/core';
import {Video} from '../shared/models/video.model';
import {Router} from '@angular/router';
import {AppPaths} from '../app-paths';
import {Id} from '../shared/models/id.model';
import {Tag, TagsColors} from '../shared/models/tag.model';
import {StorageService} from '../services/storage.service';

@Component({
  selector: 'video-details',
  templateUrl: './video-details.component.html',
  styleUrls: ['./video-details.component.scss']
})

export class VideoDetailsComponent implements OnInit {

  @Input() activeVideo: Video;
  public readonly MAIN_TAG_POS: number = 0;

  constructor(
    private router: Router,
    private localStorage: StorageService
  ) { }

  ngOnInit() {
  }

  public openPlayer(id: Id): void {
    this.router.navigate([AppPaths.PLAYER], {
      queryParams: {
        id: id,
      }
    })
  }

  public getTagContainerColor(tag: Tag): string {
    return TagsColors[tag];
  }

  public changeFavorite(id: Id): void {
    this.isFavorite(id) ? this.localStorage.removeFromFavorites(id) : this.localStorage.saveFavorite(id);
  }

  public isFavorite(id: Id): boolean {
    return this.localStorage.checkFavorite(id);
  }
}
