import {
  Component,
  ElementRef,
  EventEmitter,
  OnInit,
  Optional,
  Output,
  SkipSelf,
  ViewChild,
} from '@angular/core';
import {YoutubeGatewayService} from "../services/youtube-gateway.service";
import {Video} from '../shared/models/video.model';
import {ActivatedRoute, Params, Router} from '@angular/router';
import {StorageService} from '../services/storage.service';
import {Tag} from '../shared/models/tag.model';
import {Id} from '../shared/models/id.model';
import {Route} from '../shared/models/route.model';
import {KEYS, KeysService, NavBase, NavigatableComponent, provideNavigatable} from '@alignedcode/tvbase';

@Component({
  selector: 'video-carousel',
  templateUrl: './video-carousel.component.html',
  styleUrls: ['./video-carousel.component.scss'],
})
export class VideoCarouselComponent extends NavigatableComponent implements OnInit {

  @Output() changeActiveVideo = new EventEmitter<Video>();
  @ViewChild('videoCarouselItem',{read: ElementRef, static: false}) videoCarouselItem: ElementRef;
  public activeTag: Tag;
  private readonly MAIN_TAG_POS: number = 0;
  private readonly PAGE_ROUTE_POS: number = 1;
  private readonly FIRST_PARAMETER_ROUTE_POS: number = 2;
  private readonly SECOND_PARAMETER_ROUTE_POS: number = 3;
  private activeOffset: number = 0;
  public activeVideoIndex: number = 0;
  public videoContainerLeftPos: number = 0;
  public activePageRoute: Route;
  public activeRoute: Route;
  public videoList: Array<Video>;

  constructor(
    @SkipSelf()
    @Optional()
    parent: NavBase,
    keysService: KeysService,
    elementRef: ElementRef,
    private youtubeGatewayService: YoutubeGatewayService,
    private route: ActivatedRoute,
    private storage: StorageService,
    private router: Router,
    private storageService: StorageService,
  ) {
    super(elementRef, parent, keysService,new Map([
      [KEYS.LEFT, () => {if(this.isActive())this.prevVideo()}],
      [KEYS.RIGHT, () => {if(this.isActive())this.nextVideo()}]
    ]))
  }

  ngOnInit() {
    this.activePageRoute = this.router.url.split('/')[this.PAGE_ROUTE_POS];
    this.activeRoute = this.activePageRoute;
    this.route.params.subscribe(async (params: Params) => {
      let videoList: Array<Video> = await this.youtubeGatewayService.getPlaylistVideosWithCache();
      switch (this.activePageRoute) {
        case 'home': {
          this.videoList = videoList;
          if(this.router.url.split('/')[this.FIRST_PARAMETER_ROUTE_POS] == undefined) {
            this.router.navigate([this.activePageRoute + '/' + this.videoList[0].id]);
          }
          break;
        }
        case 'categories': {
          this.activeRoute = this.router.url.split('/')[this.PAGE_ROUTE_POS] + '/' + this.router.url.split('/')[this.FIRST_PARAMETER_ROUTE_POS];
          this.activeTag = params.tag || 'Full-body';
          if (!!this.activeTag) {
            videoList = videoList.filter(({tags}) => tags[this.MAIN_TAG_POS] === this.activeTag);
          }
          this.videoList = videoList;
          break;
        }
        case 'favorites': {
          let favoritesVideoIDs: Array<Id> = this.storage.favoritesVideoIds;
          videoList = videoList.filter(({id}) => favoritesVideoIDs.indexOf(id) != -1)
          this.videoList = videoList;

          if(this.router.url.split('/')[this.FIRST_PARAMETER_ROUTE_POS] == undefined) {
            this.router.navigate([this.activePageRoute + '/' + this.videoList[0].id]);
          }
          break;
        }
      }
    }
    )
  }

  calcOffset() {
    this.setupActiveOffset();
  }

  setupActiveOffset(){
      const videoCollection = this.videoCarouselItem.nativeElement as HTMLElement;
      const videoWidth = videoCollection.getBoundingClientRect().width;
      this.activeOffset = videoWidth;
  }

  private shiftVideos(offset): void {
    this.videoContainerLeftPos = this.videoContainerLeftPos + offset;
  }

  public nextVideo(): void {
    this.calcOffset();
    if (this.activeVideoIndex >= 0) {
      this.shiftVideos(-this.activeOffset);
    }
    if (this.activeVideoIndex === this.videoList.length - 1) {
      this.activeVideoIndex = 0;
      this.videoContainerLeftPos = 0;
    } else {
      this.activeVideoIndex++;
    }
  }

  public prevVideo(): void {
    this.calcOffset();
    if (this.activeVideoIndex !== 0) {
      this.activeVideoIndex--;
      this.shiftVideos(this.activeOffset);
    }
  }

  isActive(){
    const dom: HTMLElement = this.elementRef.nativeElement as HTMLElement;
    return  !!dom.querySelectorAll('.video-carousel-item.hover').length;
  }

  trackByFn(index, item) {
    return item.id;
  }
}
