import {ModuleWithProviders} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {AppPaths} from './app-paths';
import {environment} from 'environments/environment';
import {HomeComponent} from './pages/home/home.component';
import {VideoPlayerComponent} from './video-player/video-player.component';
import {ErrorComponent} from './pages/error/error.component';
import {CategoriesComponent} from './pages/categories/categories.component';
import {FavoritesComponent} from './pages/favorites/favorites.component';
import {ActiveVideoResolver} from './video-player/activeVideo.resolver';
import {UrlResolver} from './video-player/url.resolver';
import {PreloaderComponent} from './preloader/preloader.component';


const routes: Routes = [
  {
    path: '',
    redirectTo: `/${AppPaths.HOME}`,
    pathMatch: 'full',
  },
  {
    path: `${AppPaths.HOME}`,
    component: HomeComponent,
  },
  {
    path: `${AppPaths.HOME}/:id`,
    component: HomeComponent,
  },
  {
    path: `preloader`,
    component: PreloaderComponent,
  },
  {
    path: `${AppPaths.PLAYER}`,
    component: VideoPlayerComponent,
    runGuardsAndResolvers: 'always',
    resolve: {
      activeVideo: ActiveVideoResolver,
      url: UrlResolver,
    },
  },

  {
    path: `${AppPaths.CATEGORIES}`,
    redirectTo: `/${AppPaths.CATEGORIES}/Full-body`,
    pathMatch: 'full',
  },
  {
    path: `${AppPaths.CATEGORIES}/:tag`,
    component: CategoriesComponent,
  },
  {
    path: `${AppPaths.CATEGORIES}/:tag/:id`,
    component: CategoriesComponent,
  },
  {
    path: `${AppPaths.FAVOURITES}`,
    component: FavoritesComponent,
  },
  {
    path: `${AppPaths.FAVOURITES}/:id`,
    component: FavoritesComponent,
  },
  {
    path: '**',
    component: ErrorComponent,
  }
];

export const routing: ModuleWithProviders = RouterModule.forRoot(routes, {enableTracing: !environment.production, useHash: true});
