import {
  AfterViewInit,
  Component,
  ElementRef, Injectable,
  OnInit,
  Optional,
  Renderer2,
  SkipSelf,
  ViewChild
} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {DomSanitizer} from '@angular/platform-browser';
import {Location} from '@angular/common';
import {Url} from '../shared/models/id.model';
import {Video} from '../shared/models/video.model';
import {YoutubeGatewayService} from '../services/youtube-gateway.service';
import {Tag, TagsColors} from '../shared/models/tag.model';
import {VideoService} from '../services/video.service';
import {AppPaths} from '../app-paths';
import {KEYS, KeysService, NavBase, NavigatableComponent} from '@alignedcode/tvbase';
import {RouteEventsService} from '../services/route-events.service';

@Component({
  selector: 'player',
  templateUrl: './video-player.component.html',
  styleUrls: ['./video-player.component.scss'],
  providers: [
    { provide: Window, useValue: window }
  ]
})
@Injectable({providedIn: 'root'})
export class VideoPlayerComponent extends NavigatableComponent implements OnInit, AfterViewInit {

  @ViewChild('timeStrip',{read: ElementRef, static: false}) timeStrip: ElementRef;
  @ViewChild('timeStripPointer',{read: ElementRef, static: false}) timeStripPointer: ElementRef;
  @ViewChild('ytp-pause-overlay ytp-scroll-min ytp-scroll-max',{read: ElementRef, static: false}) relatedBar: ElementRef;
  public isPaused: boolean = false;
  public stripPointerPosition: number = 7;
  public stripPointerUpdatePosition: any;
  public videoUrl: any;
  public activeVideo: Video;
  public readonly MAIN_TAG_POS: number = 0;
  public nextVideo: Video;
  public player: any;
  public YT: object = this.window['YT'];
  public timeToEnd: string;

  constructor(
    @SkipSelf()
    @Optional()
    parent: NavBase,
    keysService: KeysService,
    elementRef: ElementRef,
    private route: ActivatedRoute,
    private location: Location,
    private videoService: VideoService,
    private youtubeGatewayService: YoutubeGatewayService,
    private router: Router,
    private sanitizer: DomSanitizer,
    private renderer: Renderer2,
    private window: Window,
    private routeEventsService: RouteEventsService,
  ) {
    super(elementRef, parent, keysService,new Map([
      [KEYS.LEFT, () => {if(this.isTimeStripActive())this.rewind()}],
      [KEYS.RIGHT, () => {if(this.isTimeStripActive())this.forward()}],
      [KEYS.REWIND, () => this.rewind()],
      [KEYS.FORWARD, () => this.forward()]
    ]))
  }

  ngOnInit() {
    this.route.data.subscribe(data => {
      this.activeVideo = data.activeVideo;
      console.log(data);
      this.videoUrl = data.url;
      this.nextVideo = this.youtubeGatewayService.getNextVideoById(this.activeVideo.id);
      this.init();
    })
  }
//TODO: refactor to viewChild
  ngAfterViewInit() {
    const timeStrip = this.timeStrip.nativeElement as HTMLElement;
    const timeStripPointer = this.timeStripPointer.nativeElement as HTMLElement;
    const timeStripWidth = timeStrip.getBoundingClientRect().width;
    const timeStripPointerWidth = timeStripPointer.getBoundingClientRect().width;
    setTimeout(()=>this.videoWatching(timeStripWidth, timeStripPointerWidth),3000)
  }

  private init(): void {
    if (this.YT) {
      this.startVideo();
      return;
    }
    let tag = document.createElement('script');
    tag.src = 'https://www.youtube.com/iframe_api';
    let firstScriptTag = document.getElementsByTagName('script')[0];
    firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);
    window['onYouTubeIframeAPIReady'] = () => this.startVideo();
  }

  private startVideo(): void {
    if (!this.player){
      this.player = new this.window['YT'].Player('player', {
        height: '100%',
        width: '100%',
        videoId: this.activeVideo.id,
        playerVars: {
          autoplay: 1,
          modestbranding: 1,
          controls: 0,
          disablekb: 1,
          rel: 0,
          showinfo: 0,
          fs: 0,
          playsinline: 1,
        }
      })}
  }

  public back(): void {
    clearInterval(this.stripPointerUpdatePosition);
    if (this.routeEventsService.getPrevMainUrl() != 'categories') {
      this.router.navigate([this.routeEventsService.getPrevMainUrl(), this.activeVideo.id]);
    }else
      this.router.navigate([
        this.routeEventsService.getPrevMainUrl(),
        this.activeVideo.tags[0],
        this.activeVideo.id]);
  }

  public getTagColor(tag: Tag): string {
    return TagsColors[tag];
  }

  public getVideoPoster(video: Video): Url {
    return this.videoService.getVideoTileImage(video);
  }

  public openNextVideoLink() {
    this.router.navigate([`/${AppPaths.PLAYER}`], {
      queryParams: {
        id: this.nextVideo.id,
      }
    });
    this.player.loadVideoById(this.nextVideo.id);
  }
//TODO: refactor to obseravable
  private videoWatching(timeStripWidth: number, timeStripPointerWidth: number): void {
      this.stripPointerUpdatePosition = setInterval(() => {
        if (!!this.player){
          this.stripPointerPosition = (this.player.getCurrentTime() / this.player.getDuration()) * (timeStripWidth - timeStripPointerWidth);
          if (this.stripPointerPosition <= 7) {
            this.stripPointerPosition = 7
        }
      }}, 1000)
  }

  public getTimeToFinish(): string {
    let timeToFinish
    if (this.player) {
      timeToFinish = this.player.getDuration() - this.player.getCurrentTime();
    } else {
      timeToFinish = this.player.getDuration();
    }
    let minutes = Math.trunc(timeToFinish / 60);
    let seconds = Math.trunc(timeToFinish % 60)
    return `${minutes}:${seconds < 10 ? '0' + seconds : seconds}`
  }

  public pauseVideo(): void {
    if (this.isPaused) {
      this.player.playVideo();
      this.isPaused = false;
    }else {
      this.player.pauseVideo();
      this.isPaused = true;
    }
  }

  private rewind(): void {
    let currentTime = this.player.getCurrentTime();
    this.player.seekTo(currentTime - 30, true);
    this.player.playVideo();
  }

  private forward(): void {
    let currentTime = this.player.getCurrentTime();
    this.player.seekTo(currentTime + 30, true);
    this.player.playVideo();
  }

  private isTimeStripActive(): boolean{
    const dom: HTMLElement = this.elementRef.nativeElement as HTMLElement;
    return  !!dom.querySelectorAll('.player__timeStrip.hover').length;
  }
}
