import {Injectable} from '@angular/core';
import {Video} from '../shared/models/video.model';
import {homeBackgroundPlaceholder,favoritesBackgroundPlaceholder} from 'app/shared/models/layout.model';
import {Url} from '../shared/models/id.model';

@Injectable({
  providedIn: 'root'
})
export class VideoService {

  constructor() { }

  public getVideoPoster(video: Video, componentName: string): Url {
    if (!video) {
      return componentName == 'Favorites' ? `url(${favoritesBackgroundPlaceholder})` : `url(${homeBackgroundPlaceholder})`;
    }
      const {picture: {maxres, standard, high, medium}} = video;
      const videoBackground = maxres ? maxres :
        standard ? standard :
          high ? high :
            medium ? medium :
              componentName == 'Favorites' ? `url(${favoritesBackgroundPlaceholder})` :
                `url(${homeBackgroundPlaceholder})`;
      return `url(${videoBackground})`;
  }

  public getVideoTileImage({picture: {medium}}): Url {
    const videoBackground = medium ? medium : homeBackgroundPlaceholder;
    return `url(${videoBackground})`;
  }
}
