import {Component, HostListener, Input, OnInit} from '@angular/core';
import {TagsColors, Tag} from '../../shared/models/tag.model';

@Component({
  selector: 'category-carousel-item',
  templateUrl: './category-carousel-item.component.html',
  styleUrls: ['./category-carousel-item.component.scss']
})
export class CategoryCarouselItemComponent implements OnInit {

  @Input() tag: Tag;
  public isActive: boolean = false;
  //TODO: refactor, think about simpler solution
  @HostListener('focusin') onFocus = () => this.isActive = true;
  @HostListener('focusout') onBlur = () => this.isActive = false;

  constructor() { }

  ngOnInit() {
  }

  public getTagColor(tag: Tag): string{
    return TagsColors[tag];
  }
}
