import { Injectable } from '@angular/core';
import {NavigationEnd, Router} from '@angular/router';
import {filter} from 'rxjs/operators';
import {Url} from '../shared/models/id.model';

@Injectable({
  providedIn: 'root'
})
export class RouteEventsService {
  private history = [];

  constructor(
    private router: Router
  ) {}

  public loadRouting(): void {
    this.router.events
      .pipe(filter(event => event instanceof NavigationEnd))
      .subscribe(({urlAfterRedirects}: NavigationEnd) => {
        this.history = [...this.history, urlAfterRedirects];
      });
  }

  public getHistory(): Url[] {
    return this.history;
  }

  public getPreviousUrl(): Url {
    return this.history[this.history.length - 2] || '/index';
  }

  public getPrevMainUrl(): Url {
    let mainRoute: Url;
    for (let route of this.history){
      if (route.split('/')[1] == 'home') mainRoute = 'home';
      if (route.split('/')[1] == 'categories') mainRoute = 'categories';
      else if(route.split('/')[1] == 'favorites') {mainRoute = 'favorites';}
    }
    return mainRoute;
  }
}
